<template>
  <div class="bgfff">
    <div class="main1 book   bgfff" style="margin-bottom: 50px;" >
      <div class="list" style="width: 100%;overflow: hidden" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <div class="item flex"  v-for="(item,index) in list" :key="index" style="padding: 10px 0;border-bottom: 1px solid #f2f2f2">
          <div class="flex1" style="padding:0 10px;">
            <img src="../../../../static/img/home/mainimg.png" style="width: 100%;height: auto">
          </div>
          <div class="flex2">
            <div class="title" style="color:#1B1B1B;margin-top: 0">别输在不会表达上+回话的技术</div>
            <div  style="text-align: right;padding: 5px 0;padding-bottom: 20px;color: #1B1B1B;font-weight: bold">审核中</div>
            <div class="bottom" style="position: absolute;bottom:10px;width: 100%">

              <div style="font-size: 12px;color: #7D7D7D;">
                <span style="color: #00D000">￥38.00 </span>| 55690人学习过
              </div>
            </div>


          </div>
        </div>
      </div>
    </div>

    <div class="pageLoad" v-if="pageLoad">
      <i class="el-icon-loading"></i>
      上划自动加载
    </div>

    <div class="bottom">
      <el-row>
        <el-col :span="12">
          <div class="item" style="background: #00CA02">
            <i class="el-icon-circle-plus-outline"></i>发布课程
          </div>
        </el-col>
        <el-col :span="12">
          <div class="item" style="background: #F39800">
            <i class="el-icon-circle-plus-outline"></i>发布课程
          </div>
        </el-col>
      </el-row>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        list:[1,4],
        pageLoad:false
      }
    },
    methods:{
      loadMore() {
        this.pageLoad = true;
        setTimeout(() => {
          let last = this.list[this.list.length - 1];
          for (let i = 1; i <= 2; i++) {
            this.list.push(last + i);
          }
          this.pageLoad = false;
        }, 2500);
      }
    },
    mounted(){

    }
  }
</script>


<style lang="less" scoped>
  .bottom{
    position: fixed;
    bottom: 0;
    width: 100%;
    .item{
      height: 50px;text-align: center;
      line-height: 50px;
      font-size: 16px;
      color: #fff;
      i{
        margin-right: 5px;
      }
    }
  }
</style>



